import Vue from 'vue'
import Router from 'vue-router'
import Custominvoice from './Custominvoice.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/custominvoice',
      name: 'Custominvoice',
      component: Custominvoice
    }
  ]
})



