                  <div class="footer-component" style=" width: 19%; float: left; margin-left: 0.75%; font-family: energicos-regular; color: #4a4a4a; font-size: 10px; letter-spacing: 0.5px; line-height: 14px;">
                    Hypovereinsbank<br/>
