<script>

  import loading from 'vue-full-loading'

  let items = [];

  export default {
    name: 'app',
    components: {
      loading
    },
    filters: {
      unescape: v => unescape(v)
    },
    data () {
      return {
        items: items,
        fields: [
          { key: 'Rechnungsdatensatz', label: 'Rechnungsdatensatz', sortable: true, 'class': 'text-center' }, // Invoice Record
          { key: 'Vertragsnummer', label: 'Vertragsnummer', sortable: true, 'class': 'text-center' }, // Contract Number
          { key: 'Rechnungsnummer', label: 'Rechnungsnummer', sortable: true, 'class': 'text-center' }, // Bill Number
          { key: 'Rechnungsdatum', label: 'Rechnungsdatum', sortable: true, 'class': 'text-center' }, // Date of Invoice
          { key: 'Kundenname', label: 'Kundenname', sortable: true, 'class': 'text-center' }, // Bill Number
          { key: 'KD_E-Mail', label: 'KD_E-Mail', sortable: true, 'class': 'text-center' }, // Email
          { key: 'Ansprechpartner', label: 'Ansprechpartner', sortable: true, 'class': 'text-center' }, // Contact Person
          { key: 'actions', label: 'View Record' },
          { key: 'invoiceactions', label: 'Generate Invoice' }
        ],
        currentPage: 1,
        perPage: 5,
        totalRows: items.length,
        pageOptions: [ 5, 10, 15 ],
        sortBy: null,
        sortDesc: false,
        filter: null,
        modalInfo: { title: '', content: '' },
        startDate: '',
        endDate: '',
        generatePDF: false,
        show: false,
        label: 'Loading...'
      }
    },
    computed: {
      sortOptions () {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => { return { text: f.label, value: f.key } })
      }
    },
    methods: {
      info (item, index, button) {
        // this.modalInfo.title = `Invoice Row No: ${index+1}`
        
        // let invoiceperview = "<h1>This is test.</h1>";
        // this.modalInfo.content = JSON.stringify(item, null, 2)
        // this.$root.$emit('bv::show::modal', 'modalInfo', button)
      },
      resetModal () {
        this.modalInfo.title = ''
        this.modalInfo.content = ''
      },
      onFiltered (filteredItems) {
        // Trigger pagination to update the number of buttons/pages due to filtering
        this.totalRows = filteredItems.length
        this.currentPage = 1
      },
      fetchByDate (startDate, endDate) {
        this.show = true;
        var self = this;
        if (startDate && endDate) {
          fetch('http://clients.yourworkwithus.com/projects/v2/invoice/findByDate?anfangsdatum='+startDate+'&endtermin='+endDate,{
            method: 'GET'
          })
          .then(response => response.json())
          .then((responseData) => {
            this.show = false;
            self.items = responseData;
            // console.log(self.items);
          })
          .catch(error => {
            this.show = false;
            alert('Record Not Found!!');
          })
        }else{
          this.show = false;
          alert('Please Enter Date Range!!');
        }
      },
      fetchByContractNum (item, index, button) {
        this.show = true;
        let contractNum = item.Vertragsnummer;
        fetch('http://clients.yourworkwithus.com/projects/v2/invoice/findByContract?vertragsnummer='+contractNum,{
          method: 'GET'
        })
        .then(response => response.json())
        .then((invoiceData) => {
          this.show = false;
          this.modalInfo.title = `Vertragsnummer : ${contractNum}`
          this.modalInfo.content = invoiceData
          this.$root.$emit('bv::show::modal', 'modalInfo', button)
          // console.log(responseData)
        })
        .catch(error => {
          this.show = false;
          alert('Record Not Found!!');
        })
      },
      generateInvoice (item, button) {
        this.show = true;
        let contractNum = item.Vertragsnummer;
        fetch('http://clients.yourworkwithus.com/projects/v2/invoice/invoiceGen?vertragsnummer='+contractNum,{
          method: 'GET'
        })
        .then(response => response.json())
        .then((invoiceData) => {
          this.show = false;

          if (invoiceData[0].status == 'success') {
            alert("Invoice has been successfully genetated.");
          }else if(invoiceData[0].status == 'already'){
            alert("Invoice already genetated.");
          }else if (invoiceData[0].status == 'p_error') {
            alert("Somthing went wrong while generating PDF.");
          }

          // console.log(invoiceData[0].status);
        })
        .catch(error => {
          this.show = false;
          alert('Invoice PDF Not Found!!');
        })
      }

    }
  }

</script>

<style>
  
  {/*  @import './assets/styles/style.scss';
  @import './css/icons-codes.css';*/}

  @font-face {
    font-family: 'energicos-bold';
    src: url('assets/styles/fonts/gilroy-bold-webfont.woff2') format('woff2'),
         url('assets/styles/fonts/gilroy-bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
      font-family: 'energicos-semibold';
      src: url('assets/styles/fonts/gilroy-semibold-webfont.woff2') format('woff2'),
           url('assets/styles/fonts/gilroy-semibold-webfont.woff') format('woff');
      font-weight: normal;
      font-style: normal;
  }

  @font-face {
      font-family: 'energicos-medium';
      src: url('assets/styles/fonts/gilroy-medium-webfont.woff2') format('woff2'),
           url('assets/styles/fonts/gilroy-medium-webfont.woff') format('woff');
      font-weight: normal;
      font-style: normal;
  }

  @font-face {
    font-family: 'energicos-regular';
    src: url('assets/styles/fonts/gilroy-regular-webfont.woff2') format('woff2'),
         url('assets/styles/fonts/gilroy-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
    font-family: 'energicos-icons';
    src: url'assets/styles/fonts/fontello.eot');
    src: url'assets/styles/fonts/fontello.eot') format('embedded-opentype'),
         url'assets/styles/fonts/fontello.woff2') format('woff2'),
         url'assets/styles/fonts/fontello.woff') format('woff'),
         url'assets/styles/fonts/fontello.ttf') format('truetype'),
         url'assets/styles/fonts/fontello.svg') format('svg');
    font-weight: normal;
    font-style: normal;
  }

  .icon-calendar[data-icon]:before {content: attr(data-icon);}
  .icon-calendar:before { content: '\e801'; }
  .icon-coins:before { content: '\e803'; }
  .icon-folder:before { content: '\e804'; }
  .icon-cabinet:before { content: '\e805'; }
  .icon-phone:before { content: '\e806'; }
  .icon-time:before { content: '\e807'; }
  .icon-users:before { content: '\e808'; }
  .icon-city:before { content: '\e809'; }

  #app {
    font-family: 'energicos-regular';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  ul {
    list-style-type: none;
  }
</style>



